import { useContext } from "react"
import Context from "../../Context/Context"
import getUsers from "../../hooks/getUsers"
import { Link } from "react-router-dom"



function TreadHeader(){
    const {setIsContacts, currentPerson, setIsTrade, setUsersDate, usersDate, setCurrentPerson, setCurrentUser, currentUser} = useContext(Context)

    async function upload(){
        await getUsers(setUsersDate)
        await setTimeout(changeUsers, 100)
    }

    async function changeUsers(){
        await usersDate.forEach((item=>{
            if( item.id == currentPerson.id) setCurrentPerson(item)
            if( item.id == currentUser.id) setCurrentUser(item)
            console.log(usersDate);
        }))
    }


    function BackBut(){
        setIsTrade(false)
    }

  
    return(
        <div className="news_header">
        <Link to='/general' style={{fontSize: '1em'}}  onClick={()=> BackBut()} className="return_news_main"><i className="fas some_little fa-arrow-left"></i> Back</Link>
        <div >{currentPerson.name}</div>
        <button style={{fontSize: '1em'}} onClick={()=> upload()} className="upload_messages"><i className="fas fa-sync-alt"></i></button>
        </div>

    )
}
export default TreadHeader
