import BioSingin from './BioSingin/BioSignin'
import BioLogin from './BioLogin/BioLogin';
import React, { useEffect, useRef } from 'react';
import { useState } from 'react';
import { useContext } from 'react';
import Context from '../../Context/Context';
import MobileLogin from './BioLogin/MobileLogin';
import MobileSignin from './BioSingin/MobileSignin';


function BioRegistratee(data){
    const { usersDate, screenWidth} = useContext(Context)
    let [register, setRegister] = React.useState(false)
    
    
    if(screenWidth > 500){
        return(
        <div className="register_bio">
            {register ? <BioSingin data={usersDate} setRegister={setRegister}/>:<BioLogin data={usersDate} setRegister={setRegister}/>}
        </div>
        )
    }
    else {
        return (
    <div className="mobile_bio">
        {register ? <MobileSignin setRegister={setRegister}/>:<MobileLogin setRegister={setRegister}/>}
        </div>
    )
    }
        
}
export default BioRegistratee